    margin: 2rem auto;
  }
  .search-bar {
    display: none;
  }
  .post-cover-info .post-title a {
    font-size: 2.2rem;
    line-height: 2.8rem;
  }
}
@media only screen and (min-width: 1240px) {
  .post-cover-info {
    font-size: 5rem;
    width: 80%;
  }
  .post-cover-info .post-title {
    line-height: 5rem;
  }
  .post-cover-info .post-title a {
    font-size: 5rem;
  }
}
@media print {
  * {
    background: transparent!important;
    color: #000!important;
    text-shadow: none!important;
    filter: none!important;
    -ms-filter: none !important;
  }
  a,
  a:visited {
    color: #444!important;
    text-decoration: underline;
  }
  a:after,
  a:visited:after {
    content: " (" attr(href) ")";
  }
  a abbr[title]:after,
  a:visited abbr[title]:after {
    content: " (" attr(title) ")";
  }
  .ir a:after,
  a[href^="javascript:"]:after,
  a[href^="#"]:after {
    content: "";
  }
  blockquote,
  pre {
    border: 1px solid #999;
    page-break-inside: avoid;
  }
  thead {
    display: table-header-group;
  }
  img,
  tr {
    page-break-inside: avoid;
  }
  img {
    max-width: 100% !important;
  }
  @page {
    margin: 0.5cm;
  }
  h2,
  h3,
  p {
    orphans: 3;
    widows: 3;
  }
  h2,
  h3 {
    page-break-after: avoid;
  }
  .page-navigation,
  .respond-form,
  .sidebar,
  .wp-prev-next,
  nav {
    display: none;
  }
}
.back-home {
  top: 4rem;
  left: 4rem;
  font-size: 2rem;
  position: relative;
  display: inline-block;
  color: rgba(250, 250, 250, 0.5);
  text-shadow: 0 0 4px rgba(164, 164, 164, 0.5);
  z-index: 1;
  font-family: Exo;
  font-weight: 800;
  opacity: 1;
  -webkit-transition: text-shadow 350ms;
  -moz-transition: text-shadow 350ms;
  -ms-transition: text-shadow 350ms;
  -o-transition: text-shadow 350ms;
  transition: text-shadow 350ms;
}
.back-home .left-arrow {
  content: '';
  display: inline-block;
  width: 1.4rem;
  height: 1.4rem;
  border: 0.4rem solid rgba(250, 250, 250, 0.5);
  background: rgba(0, 0, 0, 0);
  border-left: 0;
  border-top: 0;
  outline: 0;
  -webkit-transform: rotate(135deg);
  -moz-transform: rotate(135deg);
  -ms-transform: rotate(135deg);
  transform: rotate(135deg);
  cursor: pointer;
}
.back-home span {
  top: -1px;
  position: relative;
  left: 1px;
}
.back-home:hover {
  cursor: pointer;
  text-shadow: 0 0 14px #FFF;
}
.to-home {
  width: 48px;
  height: 48px;
  text-align: left;
  text-indent: -9999px;
  background: transparent url(..//images/to-top.png) no-repeat;
  -webkit-transform: rotate(-90deg);
  -moz-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  transform: rotate(-90deg);
  overflow: hidden;
  position: fixed;
  left: 20px;
  bottom: 20px;
  -khtml-opacity: .5;
  -moz-opacity: .5;
  -ms-filter: "alpha(Opacity=50)";
  filter: alpha(opacity=50);
  opacity: .5;
  z-index: 1000;
}
.to-home:hover {
  -khtml-opacity: .2;
  -moz-opacity: .2;
  -ms-filter: "alpha(Opacity=20)";
  filter: alpha(opacity=20);
  opacity: 0.2;
}
#nprogress {
  opacity: 0;
  pointer-events: none;
  -webkit-pointer-events: none;
}
#nprogress .bar {
  background: #f8645a;
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 20%;
  height: 3px;
  -webkit-transition: width 250ms ease-out;
  -moz-transition: width 250ms ease-out;
  -ms-transition: width 250ms ease-out;
  -o-transition: width 250ms ease-out;
  transition: width 250ms ease-out;
}
#pjax-container {
